import React from 'react';
import Accordion from 'react-bootstrap/Accordion';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert'
import "../css/styles.css";



function CreateCard(props){
    var splitTitle = props.Title.split(":");
    var fixedTitle = splitTitle[1];
    
    return(
        <div>
            <Card>
                    <Card.Header>
                    <Accordion.Toggle as={Button} variant="link" eventKey="0" aria-expanded="false">
                        {props.Title}
                    </Accordion.Toggle>
                    
                    </Card.Header>
                    <Accordion.Collapse eventKey="0">
                    <Card.Body>
                        
                        <img className = "accordion_img" src ={props.Img}></img>
                        <h3 className = "accordion_title">{fixedTitle}</h3>
                        <p className = "accordion_text">{props.Text}</p>
                        
                        <Alert key='success' variant='success'>
                        <p>Pros:</p>◾ {props.Pros}</Alert>
                        
                        <Alert key='danger' variant='danger'><p>Cons</p>◾ {props.Cons}</Alert>


                    </Card.Body>
                </Accordion.Collapse>
            </Card>
    </div>
    )
}

function Lessons(props){

    return(
        <div>
            <Accordion>

                
                <CreateCard Title={props.Title} 
                            Text ={props.Text}
                            Img ={props.Img}
                            Pros = {props.Pros}
                            Cons = {props.Cons}    
                            />
            </Accordion>
        </div>
    )

}

export default Lessons;